.App {
	text-align: center;
}

nav {
	margin: 6px;
	border: 1px black solid;
}
nav ul {
	display: flex;
	justify-content: space-evenly;
}

nav ul li {
	margin: 0 12px;
	list-style: none;
}

form label {
	display: block;
	margin: 12px;
}

.notification {
	list-style: none;
	padding-bottom: 6px;
	margin: 12px;
	border: 1px black solid;
}

.flex {
	align-items: center;
	justify-content: center;
	display: flex;
	flex-direction: column;
}

table {
	margin-left: auto;
	margin-right: auto;
}

.prodbal-wrapper {
	display: flex;
	justify-content: space-evenly;
}

@media only screen and (max-width: 1200px) {
	.prodbal-wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
	}
}

.prodbal-wrapper li {
	list-style-type: none;
}

.prodbal-wrapper input {
	width: 60px;
}

.products {
	float: right;
}

.balance {
	margin-left: 100px;
	float: left;
}

.block {
	margin: 0 auto;
	width: 400px;
}

.tabelleke > tr > td:first-child {
	width: 200px;
}
